/*--------------------------------------------------------------
## Masthead
--------------------------------------------------------------*/
#masthead {
  background-color: #d9dbe0; }
  #masthead .masthead-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-end; }
    #masthead .masthead-wrapper .site-branding img {
      width: 200px; }
    #masthead .masthead-wrapper #primary-menu {
      display: none; }
  #masthead #menu-toggle {
    background: transparent;
    margin: 0;
    padding: 0;
    border: none;
    border-radius: 0; }
  #masthead #nav-icon {
    width: 30px;
    margin: 0 auto;
    position: relative;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer; }
    #masthead #nav-icon span {
      display: block;
      position: absolute;
      height: 3px;
      width: 100%;
      background: #224289;
      opacity: 1;
      transform: rotate(0deg);
      transition: .25s ease-in-out; }
    #masthead #nav-icon span:nth-child(1) {
      top: -36px; }
    #masthead #nav-icon span:nth-child(2), #masthead #nav-icon span:nth-child(3) {
      top: -24px; }
    #masthead #nav-icon span:nth-child(4) {
      top: -12px; }
  #masthead #menu-toggle.open #nav-icon span:nth-child(1) {
    top: 18px;
    width: 0%;
    left: 50%; }
  #masthead #menu-toggle.open #nav-icon span:nth-child(2) {
    transform: rotate(45deg);
    background: #224289; }
  #masthead #menu-toggle.open #nav-icon span:nth-child(3) {
    transform: rotate(-45deg); }
  #masthead #menu-toggle.open #nav-icon span:nth-child(4) {
    top: 18px;
    width: 0%;
    left: 50%; }
  @media screen and (min-width: 400px) {
    #masthead .masthead-wrapper #nav-icon span:nth-child(1) {
      top: -36px; }
    #masthead .masthead-wrapper #nav-icon span:nth-child(2), #masthead .masthead-wrapper #nav-icon span:nth-child(3) {
      top: -23px; }
    #masthead .masthead-wrapper #nav-icon span:nth-child(4) {
      top: -9px; } }
  @media screen and (min-width: 59.375em) {
